.summary {
  text-align: center;
  max-width: 70rem;
  width: 90%;
  margin: auto;
  margin-top: 4rem;
  position: relative;
  background-color: #164269;
  color: white;
  border-radius: 7px;
  padding: 1rem;
  box-shadow: 0 1px 18px 10px rgba(0, 0, 0, 0.25);
}

.summary h2 {
  font-size: 2rem;
  margin-top: 0;
  margin-bottom: 0;
}
.spacing {
  margin-bottom: 3%;
}
.equation {
  font-weight: bold;
  color: #f7c58c;
}

